<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">{{typename}}</div>
        <el-input placeholder="搜索" v-model="searchText" class="header-right-time right" :clearable='true' @change="initData()">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="clear table-group-title">
            <div class="left breadcrumb">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{name:'administrativeManageHomepageNew'}" v-if="type==2">行政管理</el-breadcrumb-item>
                <el-breadcrumb-item>{{typename}}（{{total}}）</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <div class="right" v-if="type==1||type==3">
              <div class="table-group-title-btn btn-blue" @click="goCreatnew">
                <span class="iconfont icon-add"></span>添加新基金
              </div>
            </div>
            <div class="right" v-if="type==1||type==3">
              <label for="upload-fund-excel">
                <div class="table-group-title-btn btn-blue">
                  <span class="iconfont icon-daochu2"></span>导入基金信息
                </div>
              </label>
              <input type="file" ref="inputer" id="upload-fund-excel" style="display:none" v-on:change="uploadProjectExcel($event)" />
            </div>
            <div class="right" v-if="type==1||type==3">
              <div class="table-group-title-btn btn-blue" @click="toDownloadCusExcel">
                <span class="iconfont icon-daoru"></span>下载基金信息模版
              </div>
            </div>
            <div class="right" v-if="type==1||type==3">
              <div class="table-group-title-btn btn-blue" @click="gotoViewFundrun">
                <span class="iconfont icon-chaxun"></span>基金运行总览
              </div>
            </div>
          </div>
          <div class="table-group-tab">
            <div class="clear"></div>
            <div class="line"></div>
          </div>
          <div class="historyapply-group-tablebox table-group-tablebox">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <th>序号</th>
                  <th>基金名称</th>
                  <th>基金规模（万元）</th>
                  <th width='43'>管理人</th>
                  <th width='98'>已投项目（个）</th>
                  <th width='111'>成立时间</th>
                  <th width='285' v-if="type==1||type==3">操作</th>
                  <th width='80' v-if="type==2">操作</th>
                </tr>
                <tr v-for="(item,index) in datalist" :key="index">
                  <td>{{pageSize*(currentPage-1)+index+1}}</td>
                  <td>{{item.fundName}}</td>
                  <td v-if="item.actualSize==''||item.actualSize==null">未知</td>
                  <td v-else>{{item.actualSize}}</td>
                  <td v-if="item.name==''||item.name==null" width='43'>无</td>
                  <td v-else width='43'>{{item.name}}</td>
                  <td>{{item.com_count}}</td>
                  <td v-if="item.regTime==''||item.regTime==null" width='111'>未知</td>
                  <td v-else>{{item.regTime}}</td>
                  <td width='285' v-if="type==1||type==3">
                    <span class="table-link" @click="goFundReport(item.id,item.fundName)">基金报告</span>
                    <span class="table-link" @click="goFundRun(item.id,item.fundName)">基金运行</span>
                    
                    <span class="table-link" @click="goEditorInfo(item.id,item.fundName)">编辑</span>
                    <span class="table-link" @click="goToDelete(item.id,2)">删除</span>
                  </td>
                  <td width='80' v-if="type==2">
                    <span class="table-link" @click="toTransferManage(item.id,item.fundName)">分配管理人</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="total==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
              </div>
            </div>
            <div class="pagination-normal" v-if="total!=0">
              <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  addFundExcel,
  fundManageList,
  downloadExcel,
  downloadaddFundExcel,
  fileTemplate,
  deleteProjectAddFund
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      type: 1, //1=>基金数据2=>基金权限
      typename: '',
      currentPage: 1,
      total: null,
      pageSize: 10,
      searchText: '', //查询数据
      datalist: [], //项目列表
    }
  },
  watch: {
    $route(to, from) {
      this.init();
    }
  },
  created() {
  	if (this.$route.query.page) {
  		this.currentPage = parseInt(this.$route.query.page);
  	}
    this.init();
  },
  computed: {},
  mounted() {},
  methods: {
    init() {
      let routename = this.$route.name;
      if (routename == 'fundManage') {
        this.type = 1;
        this.typename = '基金数据';
      } else if (routename == 'fundManageSr') {
        this.type = 3;
        this.typename = '基金管理';
      } else {
        this.type = 2;
        this.typename = '基金权限';
      }
      this.initData();
    },

    initData() {
      this.datalist = [];
      let params = {
        number: this.pageSize,
        page: this.currentPage,
        search: this.searchText
      }
      if (this.type == 1 || this.type == 3) {
        params.type = 2
      } else if (this.type == 2) {
        params.type = 1
      }
      fundManageList(params).then(res => {
        if (res.data.msgNo == 1) {
          this.datalist = res.data.payload.data;
          this.total = res.data.payload.total;
        }
      })
    },
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.currentPage = val;
        this.initData();
      }
    },
    //去修改
    goEditorInfo(id, name) {
      this.$router.push({
        path: '/home/fundManageDetail',
        query: {
          id: id,
          page: this.currentPage,
          name: encodeURIComponent(name)
        }
      })
    },
    gotoViewFundrun() {
      this.$router.push({
        name: 'fundRunAllshow'
      })
    },
    goFundRun(id, name) {
      if (this.type != 3) {
        this.$router.push({
          path: '/home/fundRun',
          query: {
            id: id,
            page: this.currentPage,
            name: encodeURIComponent(name)
          }
        })
      } else {
        this.$router.push({
          path: '/home/fundRunSr',
          query: {
            id: id,
            name: encodeURIComponent(name),
            page: this.currentPage
          }
        })
      }

    },
    goFundReport(id) {
      this.$router.push({
        path: '/home/investorReport',
        query: {
          fundId: id,
          page: this.currentPage
        }
      })
    },
    //去新建一行
    goCreatnew() {
      this.$router.push({
        path: '/home/fundManageDetail',
      })
    },
    //导入项目信息文件
    uploadProjectExcel(e) {
      let inputDOM = this.$refs.inputer;
      // 通过DOM取文件数据
      let file = inputDOM.files[0];
      if (file == undefined) {
        return false;
      }
      // console.log(file);
      // console.log(file.name);
      if (!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
        dialogConfirm('info', '请选择Excel格式文件')
        inputDOM.value = '';
        return false
      }
      addFundExcel({}, file).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '导入成功');
          this.currentPage = 1;
          inputDOM.value = '';
          this.initData();
        } else {
          dialogConfirm('info', res.data.message);
        }
      })
    },
    //下载模板
    toDownloadCusExcel() {
      fileTemplate({
        fileName:'jjdrmb'
      }).then(res => {
        if (res.data.msgNo == 1) {
          window.location.href = res.data.payload;
        } else {
          dialogConfirm('info', res.data.message)
        }
      });
    },
    toTransferManage(id, name) {
      this.$router.push({
        path: '/home/fundManageToSet',
        query: {
          id: id,
          page:this.currentPage,
          name: encodeURIComponent(name),
          
        }
      })
    },
    goToDelete(id, type) {
      dialogConfirmHasbtn('确定要删除该基金？').then(() => {
        deleteProjectAddFund({
          type: type,
          id: id,
        }).then(res => {
          console.log(res);
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '删除成功');
            this.initData();
          } else {
            dialogConfirm('info', res.data.message)
          }
        });
      }).catch(() => {});
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.table-group-title-btn {
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
    //float: left;
    //margin-top: 5px;
    margin-right: 5px;
  }
  .icon_add {
    background: url('../../images/projectpermissions_icon.png') no-repeat;
    background-size: 100%;
  }
  .icon_upload {
    background: url('../../images/icon_upload.png') no-repeat;
    background-size: 90%;
  }
  .icon_download {
    background: url('../../images/icon_download.png') no-repeat;
    background-size: 100%;
    //margin-top: 4px;
  }
}

.peopleSelectTitle-text {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 40px;
  margin-right: 10px;
}

.peopleSelectTitle {
  border-bottom: 1px solid #dcdfe6;
}

.table-link-disable {
  color: #35383A
}

</style>



